.services {
  padding-top: 64px;
  padding-bottom: 140px;

  background-color: $color-black-pearl;

  @include vp-1023 {
    padding-top: 54px;
    padding-bottom: 75px;
  }

  @include vp-767 {
    padding-top: 47px;
    padding-bottom: 86px;
  }
}

.services__content p {
  margin: 0;
  margin-bottom: 54px;

  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: 400;
  font-size: 36px;
  line-height: 51px;
  color: $color-default-white;

  @include vp-1023 {
    margin-bottom: 37px;
  }

  @include vp-767 {
    margin-bottom: 29px;

    font-size: 28px;
    line-height: 39px;
  }
}

.services__content p .long {
  @include vp-767 {
    display: none;
  }
}

.services__content p .short {
  display: none;

  @include vp-767 {
    display: block;
  }
}

.services__content ul {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  column-gap: 85px;
  row-gap: 85px;
  margin: 0;
  padding: 0;

  list-style: none;

  @include vp-1279 {
    grid-template-columns: repeat(2, 1fr);
  }

  @include vp-1023 {
    grid-template-columns: auto;
    row-gap: 30px;
  }
}

.services__content a {
  position: relative;

  display: block;
  width: 100%;
  min-height: 650px;
  padding: 47px 80px 50px 40px;

  background-color: $color-tangaroa;
  background-image: url("../img/services.jpg");
  background-repeat: no-repeat;
  background-size: 1493px 817px;

  &::before {
    content: "";

    position: absolute;
    top: 0;
    left: 0;

    width: 100%;
    height: 100%;

    background-color: $color-black-pearl-50;
  }

  &::after {
    content: "";

    position: absolute;
    right: 0;
    bottom: 0;

    width: 60px;
    height: 60px;

    background-color: $color-deep-sky-blue;
    background-image: url("../img/svg/arrow.svg");
    background-repeat: no-repeat;
    background-position: center;
    background-size: 5px 10px;
    border-radius: 3px 0 0 0;

    @include vp-767 {
      width: 40px;
      height: 40px;
    }
  }

  @include vp-1023 {
    min-height: 250px;
    padding: 38px 45px;
  }

  @include vp-767 {
    padding: 26px 30px;
  }
}

.services__link--1 {
  background-position: calc(50% + 383px) calc(50% - 34px);

  @include vp-1023 {
    background-position: calc(50% + 166px) calc(50% - 264px);
  }

  @include vp-767 {
    background-position: calc(50% + 236px) calc(50% - 16px);
  }
}

.services__link--2 {
  background-position: calc(50% + 37px) calc(50% - 34px);

  @include vp-1023 {
    background-position: calc(50% - 214px) calc(50% - 29px);
  }

  @include vp-767 {
    background-position: calc(50% + 246px) calc(50% - 253px);
  }
}

.services__link--3 {
  background-position: calc(50% - 322px) calc(50% - 34px);

  @include vp-1023 {
    background-position: calc(50% + 264px) calc(50% - 75px);
  }

  @include vp-767 {
    background-position: calc(50% - 271px) calc(50% - 110px);
  }
}

.services__content h3 {
  position: relative;

  margin: 0;

  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: 400;
  font-size: 20px;
  line-height: 30px;
  color: $color-default-white;

  @include vp-767 {
    font-size: 18px;
    line-height: 30px;
  }
}
